.row:after {
  display: table;
  clear: both;
  content: ''; }

[class^="col-"] {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box; }

.container {
  position: relative;
  max-width: 1140px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px; }

.row {
  position: relative;
  margin-left: -15px;
  margin-right: -15px; }
  .row .row {
    margin-left: 0;
    margin-right: 0; }

[class^="col-"] {
  position: relative;
  float: left;
  min-height: 1px;
  padding-left: 15px;
  padding-right: 15px; }

